export default {
	props: {
		TrendChart: {
			type: Array
		},
		dotTitle: {
			type: Array
		},
		title: {
			type: String,
			default: '湖北/非湖北'
		}
	},
	data() {
		return {
			cardCur: 0 //轮播图初始化状态
		};
	},
	methods: {
		handleChange(e) {
			this.cardCur = e.detail.current;
		},
		// 轮播图点击切换状态
		handleActive(index) {
			this.cardCur = index;
		},
		//轮播图pre事件
		hanglePre() {
			this.cardCur--;
			if (this.cardCur < 0) {
				this.cardCur = this.TrendChart.length;
			}
		},
		// 轮播图next事件
		handleNext() {
			this.cardCur++;
			console.log(this.TrendChart.length)
			if (this.cardCur > this.TrendChart.length - 1) {
				this.cardCur = 0;
			}
		}
	}
};
